/* src/app/features/dashboard/dashboard-page/dashboard-page.scss */
.dashboard-welcome-card {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  margin: 0 auto;
}
.dashboard-welcome-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}
.dashboard-welcome-header h1 {
  margin: 0;
  color: #333;
  font-size: 2rem;
  font-weight: 600;
}
.dashboard-welcome-header .dashboard-icon {
  font-size: 2.5rem;
  width: 2.5rem;
  height: 2.5rem;
  color: #667eea;
}
.dashboard-welcome-header .user-info {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 0.9rem;
  color: #666;
}
.dashboard-welcome-header .user-info .logout-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.9rem;
}
.dashboard-welcome-header .user-info .logout-btn:hover {
  background: #e9ecef;
  border-color: #adb5bd;
}
.dashboard-welcome-header .user-info .logout-btn mat-icon {
  font-size: 1rem;
  width: 1rem;
  height: 1rem;
}
.dashboard-subtitle {
  color: #666;
  font-size: 1.1rem;
  margin-bottom: 2rem;
  text-align: center;
}
.dashboard-feature-list {
  list-style: none;
  padding: 0;
  margin: 0 0 2rem 0;
}
.dashboard-feature-list li {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  margin-bottom: 0.5rem;
  background: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #667eea;
  transition: transform 0.2s ease;
}
.dashboard-feature-list li:hover {
  transform: translateX(4px);
}
.dashboard-feature-list li mat-icon {
  color: #667eea;
  font-size: 1.5rem;
  width: 1.5rem;
  height: 1.5rem;
}
.dashboard-feature-list li strong {
  color: #333;
}
.dashboard-get-started {
  text-align: center;
  color: #666;
  font-style: italic;
  margin: 0;
}
@media (max-width: 700px) {
  .dashboard-welcome-card {
    padding: 1.2rem 0.7rem 1.2rem 0.7rem;
    max-width: 98vw;
  }
  .dashboard-welcome-header h1 {
    font-size: 1.3rem;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
.feature-card-header mat-icon {
  font-size: 2.2rem;
  height: 2.2rem;
  width: 2.2rem;
  min-width: 2.2rem;
  min-height: 2.2rem;
  color: #1976d2;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  vertical-align: middle;
  position: relative;
  top: 0;
}
/*# sourceMappingURL=dashboard-page.css.map */
